$size: 100px;
$day-gap: 1px;
$day-padding: 10px;
$calendar-width: 7*$size+6*$day-gap+2*$day-padding;
$days-of-week:
  "mon" 0,
  "tue" ($size+$day-gap),
  "wed" ($size+$day-gap)*2,
  "thu" ($size+$day-gap)*3,
  "fri" ($size+$day-gap)*4,
  "sat" ($size+$day-gap)*5,
  "sun" ($size+$day-gap)*6;
